<template>
  <div class="flex flex-row w-screen h-screen">
    <div
      class="w-1/4 flex bg-red-100 flex-col p-4 max-h-screen overflow-y-scroll border-r noscrollbar"
    >
      <div class="flex rounded-lg w-full h-auto p-2 bg-white"></div>
    </div>
    <div class="w-3/4 flex flex-col p-4 max-h-screen overflow-y-scroll">
      <div class="rounded-lg bg-white border shadow-md relative p-4">
        <div class="p-2 flex flex-col">
          <div class="flex flex-row w-full justify-between mb-2">
            <span>states : </span>
            <input class="border mx-2" type="text" v-model="rawInput.states" />
            <div class="w-1/3"></div>
          </div>
          <div class="flex flex-row w-full justify-between mb-2">
            <span>start : </span>
            <input class="border mx-2" type="text" v-model="rawInput.start" />
            <div class="w-1/3"></div>
          </div>
          <div class="flex flex-row w-full justify-between mb-2">
            <span>alphabet : </span>
            <input
              class="border mx-2"
              type="text"
              v-model="rawInput.alphabet"
            />
            <div class="w-1/3"></div>
          </div>
          <div class="flex flex-row w-full justify-between mb-2">
            <span>finish : </span>
            <input class="border mx-2" type="text" v-model="rawInput.finish" />
            <div class="w-1/3"></div>
          </div>

          <div class="flex flex-col">
            <span>table :</span>
            <span>
              {{ rawtable }}
            </span>
          </div>
          <div class="flex flex-row flex-wrap w-full mb-2 bg-blue-300 p-2">
            <select class="mx-1" v-model="tempT.state">
              <option disabled value="">Please select one</option>
              <option
                v-for="state in parsedInput.states"
                :key="state"
                :value="state"
              >
                {{ state }}
              </option>
            </select>
            <div class="bg-blue-100 rounded-md p-2">
              <div
                class="mr-1 pb-4"
                v-for="item in parsedInput.alphabet"
                :key="item"
              >
                <span class="bg-white p-1"> {{ item }}</span>
                <span>></span>
                <select class="px-1" v-model="tempT.table[item]">
                  <option disabled value="">Please select one</option>
                  <option
                    v-for="state in parsedInput.states"
                    :key="state"
                    :value="state"
                  >
                    {{ state }}
                  </option>
                </select>
              </div>
              <!-- <div class="mr-1">
                <select class="mx-1">
                  <option disabled value="">Please select one</option>
                  <option>A</option>
                  <option>B</option>
                  <option>C</option>
                </select>
                <span>></span>
                <select class="mx-1">
                  <option disabled value="">Please select one</option>
                  <option>A</option>
                  <option>B</option>
                  <option>C</option>
                </select>
              </div> -->
            </div>

            <button
              class="p-4 border border-green-500 rounded-lg bg-white"
              @click="addTransition"
            >
              Add
            </button>
          </div>
        </div>
        <button class="bg-black text-white p-4 m-2" @click="generate">
          generate
        </button>
        <button class="bg-black text-white p-4 m-2" @click="reset">
          reset
        </button>
      </div>
      <div class="rounded-lg bg-white border shadow-md relative p-4">
        <!-- <button
          class="bg-black text-white top-0 right-0 absolute w-8 h-8 focus:outline-none"
        >
          X
        </button> -->
        <div v-if="ready">
          <div class="ct mt-6">
            <network
              v-if="inputTable.edges"
              class="wrapper"
              ref="network"
              :nodes="inputTable.nodes"
              :edges="inputTable.edges"
              :options="options"
            >
            </network>
          </div>
          <div class="ct mt-6">
            <network
              v-if="mininpTable.edges"
              class="wrapper"
              ref="network"
              :nodes="mininpTable.nodes"
              :edges="mininpTable.edges"
              :options="options"
            >
            </network>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import minimize from "./minimize";
import { bus } from "./minimize";
import "vue-vis-network/node_modules/vis-network/dist/vis-network.css";
// import { nextTick } from "vue/types/umd";

export default {
  mounted() {
    bus.$on("nextnodes", (e) => {
      this.reachableStack.push(e);
    });
    bus.$on("minimize", (e) => {
      //   this.$emit("minimize", e);
      console.log(e);
    });
    const transition = {
      q0: { a: "q1", b: "q3" },
      q1: { a: "q0", b: "q3" },
      q2: { a: "q1", b: "q4" },
      q3: { a: "q5", b: "q5" },
      q4: { a: "q3", b: "q3" },
      q5: { a: "q5", b: "q5" },
    };

    const start = "q0";

    const alphabet = ["a", "b"];

    const finish = ["q3", "q5"];
    this.originalDFA = { table: transition, start, alphabet, finish };
    this.originalTable = this.toVisDfa(this.originalDFA);
    this.minDFA = minimize(transition, alphabet, start, finish);
    this.minTable = this.toVisDfa(this.minDFA);
  },
  methods: {
    generate() {
      this.inputTable = this.toVisDfa({
        table: this.rawtable,
        start: this.parsedInput.start,
        finish: this.parsedInput.finish,
      });
      console.log(
        "rawt",
        this.rawtable,
        this.parsedInput.alphabet,
        this.parsedInput.start,
        this.parsedInput.finish
      );
      let minDFA = minimize(
        this.rawtable,
        this.parsedInput.alphabet,
        this.parsedInput.start,
        this.parsedInput.finish
      ).table;
      this.mininpDFA = minDFA;
      this.mininpTable = this.toVisDfa({
        table: minDFA,
        start: this.parsedInput.start,
        finish: this.parsedInput.finish,
      });
      console.log(this.rawtable, this.inputTable, minDFA, this.mininpTable);
      this.ready = true;
    },
    reset() {},
    addTransition() {
      let tempT = this.tempT;

      this.rawtable[tempT.state] = tempT.table;
      this.tempT = { table: {} };
    },
    toVisDfa(dfa) {
      console.log("vis", dfa);
      let table = dfa.table;
      let nodes = [];
      let edges = [];
      let map = {};
      if (table) {
        let keys = Object.keys(table);
        for (let i in keys) {
          let color = "white";
          if (i === dfa.start) {
            color = "green";
          }
          for (let j of dfa.finish) {
            if (j === i) {
              color = "red";
            }
          }
          nodes.push({ id: i, label: keys[i], shape: "circle", color });
          map[keys[i]] = i;
        }
        for (let i in keys) {
          for (let alpha of this.originalDFA.alphabet) {
            edges.push({
              arrows: "to",
              label: alpha,
              from: i,
              to: map[table[keys[i]][alpha]],
            });
          }
        }
      }
      return { table, nodes, edges, map };
    },
  },
  computed: {
    parsedInput() {
      let start = this.rawInput?.start;
      if (start) {
        start = start.split(",")[0];
      }
      let finish = this.rawInput?.finish;
      if (finish) {
        finish = finish.split(",");
      }
      let alphabet = this.rawInput?.alphabet;
      if (alphabet) {
        alphabet = alphabet.split(",");
      }
      let states = this.rawInput?.states;
      if (states) {
        states = states.split(",");
      }
      return { start, finish, alphabet, states };
    },
  },
  data() {
    return {
      rawInput: {},
      rawtable: {},
      tempT: { table: {} },
      inputTable: {},
      mininpTable: {},
      ready: false,
      reachableStack: [],
      originalDFA: {},
      originalTable: {},
      message: "Render network with Vue-vis-network",
      minDFA: {},
      minTable: {},
      options: {
        zoom: 10,
        height: "100%",
        width: "100%",
        nodes: {
          borderWidth: 1,
          color: "white",
        },
        edges: {
          color: "black",
        },
      },
      reachLevel: 0,
    };
  },
};
</script>

