const nextPartition = (partition, transition, alphabet) => {
  const next = [];

  for (const i of partition) {
    const temp = [];
    if (i.length === 1) {
      temp.push([i[0]]);
    }
    if (i.length > 1) {
      temp.push([i[0]]);
      for (let j = 1; j < i.length; j++) {
        let flagIsPushed = false;
        for (let jj of temp) {
          let flagAreDistinguishable = areDistinguishable(
            jj[0],
            i[j],
            alphabet,
            partition,
            transition
          );
          if (!flagAreDistinguishable) {
            jj.push(i[j]);
            flagIsPushed = true;
          }
        }

        if (!flagIsPushed) {
          temp.push([i[j]]);
        }
      }
    }
    next.push(...temp);
  }
  return next;
};

const partition = (startingPartition, transition, alphabet) => {
  const partitioning = { p0: startingPartition };
  let level = 0;
  let PartitionLen = startingPartition.length;
  let done = false;
  let p;
  while (!done) {
    level += 1;
    p = nextPartition(startingPartition, transition, alphabet);
    partitioning["p" + level] = p;
    done = p.length === PartitionLen;
    PartitionLen = p.length;
  }
  return { partitioning, last: p };
};

const transitionMap = (partition) => {
  let map = {};
  let rMap = {};
  for (let item of partition) {
    let joinedName = item.join("");
    if (joinedName) {
      for (let node of item) {
        map[node] = joinedName;
      }
      rMap[joinedName] = item;
    }
  }

  return { map, rMap };
};

// Function to minimize the given transition table
const minimize = (table, alphabet, start, finish) => {
  let T = {};
  for (let item in table) {
    T[item] = table[item];
  }
  console.log("from minimize", { T, alphabet, start, finish });
  const reachable = removeUnrechable(T, start, alphabet);
  bus.$emit("reached", {
    title: "table traverse done",
    table: reachable,
    start: start,
    alphabet: alphabet,
    finish: finish,
  });
  const nonFinish = Object.keys(reachable).filter((item) => {
    return !finish.includes(item);
  });
  const startingPartition = [finish, nonFinish];
  const partitions = partition(startingPartition, reachable, alphabet);
  const result = partitions.last;

  const transitionM = transitionMap(result);

  const resultingTransition = {};
  Object.keys(transitionM.rMap).forEach((key) => {
    resultingTransition[key] = {};
    alphabet.forEach((alpha) => {
      resultingTransition[key][alpha] =
        transitionM.map[table[transitionM.rMap[key][0]][alpha]];
    });
  });

  let minFinish = [];
  for (let item of finish) {
    minFinish.push(transitionM.map[item]);
  }
  bus.$emit("minimize", {
    title: "Minimsation Done",
    table: resultingTransition,
    start: transitionM.map[start],
    alphabet: alphabet,
    finish: Array.from(new Set(minFinish)),
  });
  //   console.log(resultingTransition);
  return {
    table: resultingTransition,
    start: transitionM.map[start],
    alphabet: alphabet,
    finish: Array.from(new Set(minFinish)),
  };
};

// minimize(transition, alphabet, start, finish);
export default minimize;
